@import '../../styles/utils.scss';

.uploadWallPaperQuestion-wrapper{
    width:100%;
    display: flex;
    align-items: center;
    margin-bottom: 1.6rem;
    .number{
        width:2.2rem;
        height:2.2rem;
        background-color: $black10;
        border-radius: 50%;
        @include font-face(Pretendard-SemiBold, 1.4rem, 1.8rem, $primary60);
        padding:0.2rem 0.75rem;
        margin-right:1.2rem;
    }
    .title{
        @include font-face(Pretendard-Medium, 1.8rem, 2.6rem, $black90);
        margin-right:0.4rem;
    }
    span{
        @include font-face(Pretendard-Regular, 1.4rem, 2.2rem, $black60);
        margin:0.2rem 0;
    }

}