.welcomepage-wrapper {
    width: 100vw;
    height : 100vh;
    flex-grow : 0;
    animation-name : background;
    animation-duration: 2s ;
    animation-fill-mode: forwards;
    padding-top : 15rem;  
    padding-bottom : 12.8rem; 
    .text-logo{
        width : 18.4rem;
        height : 4.5rem; 
        flex-grow : 0;
        display : block ; 
        margin : auto;
        animation-name : textLogo; 
        animation-duration: 2s;
        animation-fill-mode: forwards;
    }
    .intro{
        margin: 2.4rem 1rem 0 0.9rem;
        font-family: 'Pretendard-Regular';
        font-size: 1.6rem;
        line-height: 1.5;
        letter-spacing: normal;
        text-align: center;
        color: #474451;
        animation : intro 2s forwards;
    }    
    .frame{
        margin : 13.6rem 0rem 2.4rem 0rem; 
        .talk-box{
            display: block;
            margin: auto;
            animation: talk-box 2s forwards;
        }
        .social-wrap{
            margin-top : 2.4rem; 
            display: flex;
            justify-content: space-evenly;
            animation : social-wrap 2s forwards; 
        }
    }
    p{
        font-family: 'Pretendard-Medium';
        color : #595663; 
        font-size: 1.2rem;
        font-weight: 500;
        line-height: 1.67;
        text-align: center;
    }
    .see-long-term-wrapper{
        margin-top : 1.6rem;
        .see-long-term{
            width: max-content;
            margin: auto; 
            border-bottom : 1px solid #996eff; 
            cursor : pointer; 
            text-align: center;
            font-family: 'Pretendard-Medium';
            color : #996eff; 
            font-size: 1.2rem;
            font-weight: 500;
            line-height: 1.67; 
            &:nth-child(1){
                margin-top: 1.2rem; 
            }
            &:nth-child(2){
                margin-top: 1.2rem; 
            }
        }
    }
}



@keyframes background {
    from {
        background-color: #5b21ff;
    }
    to { 
        background-color: white;
    }
}

@keyframes textLogo {
    from {
            fill : white; 
    }
    to { 
            fill : #5b21ff;
    }
}

@keyframes social-wrap{
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes talk-box{
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    75%{
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes intro{
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}